class FFmpegOptions {

  public values:string[] = [];

  constructor(width:number,height:number,fps:number=60,fileName:string="video.mp4"){

    this.values.push("-y");
    this.values.push("-f");           this.values.push("rawvideo");
    this.values.push("-s");           this.values.push(width+"x"+height);
    this.values.push("-pix_fmt");     this.values.push("rgb0");
    this.values.push("-r");           this.values.push(""+fps);
    this.values.push("-i");           this.values.push("-");

    //this.values.push("-c:v");         this.values.push("libx264");
    //this.values.push("-preset");      this.values.push("slow");
    //this.values.push("-crf");         this.values.push("22");
    this.values.push("-an");          this.values.push(fileName);

  }



}
